import { getPageElements } from '../page-elements';
import FilterPresenter from './filter';
// import InfoPresenter from './info';
import ListPresenter from './list';
import AddEventButtonPresenter from './new-point-button';
import SortingPresenter from './sorting';
export default class MainPresenter {
    #elements = getPageElements();
    #service;
    #models;
    // #info: InfoPresenter | null = null;
    #filter = null;
    #addButton = null;
    #sorting = null;
    #list = null;
    constructor({ service, models }) {
        this.#service = service;
        this.#models = models;
        // this.#info = new InfoPresenter({container: this.#elements.header as HTMLElement, this.#models })
        this.#filter = new FilterPresenter({ container: this.#elements.filters, models: this.#models });
        this.#addButton = new AddEventButtonPresenter({ container: this.#elements.header, onAddButtonClick: this.#handleAddButton });
        this.#sorting = new SortingPresenter({ container: this.#elements.events, models: this.#models });
        this.#list = new ListPresenter({ container: this.#elements.events, models: this.#models });
    }
    init() {
        this.#filter?.init();
        this.#addButton?.init();
        this.#sorting?.init();
        this.#list?.init();
    }
    #handleAddButton = () => {
        if (this.#list !== null) {
            this.#list.createPoint();
        }
    };
}
